import React, { memo, forwardRef } from "react";
import PropTypes from "prop-types";
import ClassNames from "classnames";

import "./Icon.styl";
const Icon = forwardRef(({ className, iconId, onClick }, ref) =>
    iconId ? (
        <div
            className={ClassNames("Icon", className, `Icon-${iconId}`)}
            ref={ref}
            onClick={onClick}
        >
            <div className="Container Flex Flex-middle Flex-center">
                <svg className={ClassNames("Icon-svg")}>
                    <use xlinkHref={`#icon-${iconId}`}></use>
                </svg>
            </div>
        </div>
    ) : null
);
Icon.displayName = "Icon";
Icon.propTypes = {
    children: PropTypes.oneOfType([
        PropTypes.string,
        PropTypes.node,
        PropTypes.arrayOf(PropTypes.node)
    ]),
    className: PropTypes.string,
    iconId: PropTypes.string,
    onClick: PropTypes.func
};
export default memo(Icon);
